import React, { useRef, useState } from "react"
import { Form, Button, Card, Alert } from "react-bootstrap"
import { useAuth } from '../contexts/AuthContexts'
import { Link, useHistory } from "react-router-dom"
import GoogleLogin from 'react-google-login'

export default function Login() {
  const emailRef = useRef()
  const passwordRef = useRef()
  const { login } = useAuth()
  const [error, setError] = useState("")
  const [loading, setLoading] = useState(false)
  const history = useHistory()

  async function handleSubmit(e) {
    e.preventDefault()

    try {
      setError("")
      setLoading(true)
      await login(emailRef.current.value, passwordRef.current.value)
      history.push("/")
    } catch {
      setError("Failed to log in")
    }

    setLoading(false)
  }

  function responseGoogle(response){
        try {
            console.log(response);
            console.log(response.profileObj);
            setError("")
            setLoading(true)
            history.push("/")
        }
        catch {
            setError("Failed to log in")
          }
    }

    function responseGooglefail(response) {
        console.log(response);
        console.log(response.profileObj);
    }

  return (
    <>
      <Card>
        <Card.Body>
          <h2 className="text-center mb-4">Log In</h2>
          {error && <Alert variant="danger">{error}</Alert>}
          <Form onSubmit={handleSubmit}>
            <Form.Group id="email">
              <Form.Label>Email</Form.Label>
              <Form.Control type="email" ref={emailRef} required />
            </Form.Group>
            <Form.Group id="password">
              <Form.Label>Password</Form.Label>
              <Form.Control type="password" ref={passwordRef} required />
            </Form.Group>
            <Button disabled={loading} className="w-50" type="submit">
              Log In
            </Button>
          </Form>
          
          <div className="w-50 text-center mt-3">
        <GoogleLogin
            clientId="670425971519-6qhk3njjh26vhupanmltnqjoqi5sktae.apps.googleusercontent.com"
            buttonText="Login"
            onSuccess={responseGoogle}
            onFailure={responseGooglefail}  
            cookiePolicy={'single_host_origin'}
         />
        </div>
        </Card.Body>
      </Card>
      <div className="w-50 text-center mt-2">
        Need an account? <Link to="/signup">Sign Up</Link>
      </div>
    </>
  )
}