import React, { useRef, useState } from 'react'
import { Form, Button, Card, Alert, Row, Col } from "react-bootstrap"
import { useAuth } from  '../contexts/AuthContexts'
import { Link, useHistory } from 'react-router-dom'

function Signup() {

    const firstName = useRef()
    const lastName = useRef()
    const phone = useRef()
    const emailRef = useRef()
    const passwordRef = useRef()
    const passwordConfirmRef = useRef()
    const { signup } = useAuth()
    const [error, setError] = useState("")
    const[loading, setLoading] = useState(false)
    const history = useHistory()

    async function handleSubmit(e) {
        e.preventDefault()

        if (passwordRef.current.value !== passwordConfirmRef.current.value) {
            return setError("Passwords do not match")
        }

        try {
            setError("")
            setLoading(true)
            await signup(emailRef.current.value, passwordRef.current.value)
            history.push('/')
         
          } catch {
            setError("Failed to create an account")
          }
      
          setLoading(false)
    }

    return (
        <>
            <Card style = {{ padding: "5%" }}>
                <Card.Body>
                    <h2 className="text-center_mb-4">Register</h2>
                    <h5 style = {{ marginTop: "80px", marginBottom: "40px" }}>Manage all your lottery efficiently</h5>
                    <p style = {{ marginBottom: "40px"}}>Let's get you all set up so you can verify your personal account and begin setting up your profile</p>
                    {error && <Alert variant = "danger"> {error} </Alert>}
                    <Form onSubmit = {handleSubmit}>
                        <Row>
                            <Col>
                            <Form.Group id="firstName">
                                <Form.Label>First Name</Form.Label>
                                <Form.Control type="text" ref={firstName} required />
                            </Form.Group>
                            </Col>
                            <Col>
                            <Form.Group id="lastName">
                                <Form.Label>Last Name</Form.Label>
                                <Form.Control type="text" ref={lastName} required />
                            </Form.Group>
                            </Col>
                        </Row>


                        <Row>
                            <Col>
                            <Form.Group id="phone">
                                <Form.Label>Phone Number</Form.Label>
                                <Form.Control type="number" ref={phone} required />
                            </Form.Group>
                            </Col>
                            <Col>
                            <Form.Group id="email">
                                <Form.Label>Email</Form.Label>
                                <Form.Control type="email" ref={emailRef} required />
                            </Form.Group>
                            </Col>
                        </Row>

                        <Row>
                            <Col>
                            <Form.Group id="password">
                                <Form.Label>Password</Form.Label>
                                <Form.Control type="password" ref={passwordRef} required />
                            </Form.Group>
                            </Col>
                            <Col>
                            <Form.Group id="confirmPassword">
                                <Form.Label>Confirm Password</Form.Label>
                                <Form.Control type="password" ref={passwordConfirmRef} required />
                            </Form.Group>
                            </Col>
                        </Row>

                        <Form.Group id="formGridCheckbox">
                            <Form.Check type="checkbox" label="Yes, I want to receive lottery display emails" required/>
                        </Form.Group>

                        <Form.Group id="formGridCheckbox">
                            <Form.Check type="checkbox" label="I agree to all the Term, Privacy and Fees" required/>
                        </Form.Group>

                        <Button disabled = {loading} varient = "primary"  type="submit" active>
                            Create Account
                        </Button>
                        
                    </Form>
                </Card.Body>
            </Card>
            <div className="w-100_text-center_mt-2" style = {{ margin: "20px", padding: "10px"}}>
                Already have an account? <font color = "blue"><b><Link to= "/login">Log In</Link></b></font>
            </div>
        </>
    )
}

export default Signup
